/* This is the css file corresponding to pages with surveys listed */
.surveyList {
	padding: 1vw 2vw;
	width: 100%;
	height: 70vh;
	overflow-y: auto;
	overflow-x: hidden;
}
.survey {
	width: 93%;
	-webkit-box-shadow: 0 .2vw .3vw 0 rgba(0, 0, 0, 0.05), 0 .1vw .5vw 0 rgba(0, 0, 0, 0.05);
	        box-shadow: 0 .2vw .3vw 0 rgba(0, 0, 0, 0.05), 0 .1vw .5vw 0 rgba(0, 0, 0, 0.05);
	-webkit-transition: -webkit-box-shadow .2s ease-in-out;
	transition: -webkit-box-shadow .2s ease-in-out;
	-o-transition: box-shadow .2s ease-in-out;
	transition: box-shadow .2s ease-in-out;
	transition: box-shadow .2s ease-in-out, -webkit-box-shadow .2s ease-in-out;
	padding: 1.5vw;
	margin-bottom: 3.5vw;
  -webkit-transition: opacity .2s ease-in-out;
  -o-transition: opacity .2s ease-in-out;
  transition: opacity .2s ease-in-out;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: baseline;
      justify-content: baseline;

}
.survey:hover {
	-webkit-box-shadow: 0 .2vw .3vw 0 rgba(0, 0, 0, 0.08), 0 .1vw .5vw 0 rgba(0, 0, 0, 0.08);
	        box-shadow: 0 .2vw .3vw 0 rgba(0, 0, 0, 0.08), 0 .1vw .5vw 0 rgba(0, 0, 0, 0.08);
	cursor: pointer;
}
.surveyArtwork {
  width: 6vw;
  padding-left: 1vw;
  padding-right: 1vw;
}
.surveyArtwork img {
  max-width: 6vw;
  max-height: 6vw;
  width: 100%;
  height: 100%;
}
.surveyTitle {
	margin: 1.5vw 0;
	margin-top: 0;
}
.surveyTitle span {
	font-size: 1.25vw;
	color: #000000;
	margin: 1vw;
	-webkit-transition: .2s color ease-in-out;
	-o-transition: .2s color ease-in-out;
	transition: .2s color ease-in-out;
}
.surveyTitle span:hover {
	color: #7fbb00;
	text-decoration: underline;

}
.surveyDescription span {
	font-size: .9vw;
	margin: 1vw;
}

/* Animations Used On The Webpage */
.zoomInOut {
  -webkit-animation: zoominout .3s forwards;
          animation: zoominout .3s forwards;
}
@-webkit-keyframes zoominout {
  0% {
    -webkit-transform: scale(.95);
            transform: scale(.95);
  }
  10% {
    -webkit-transform: scale(.955);
            transform: scale(.955);
  }
  20% {
    -webkit-transform: scale(.96);
            transform: scale(.96);
  }
  30% {
    -webkit-transform: scale(.965);
            transform: scale(.965);
  }
  40% {
    -webkit-transform: scale(.97);
            transform: scale(.97);
  }
  50% {
    -webkit-transform: scale(.975);
            transform: scale(.975);
  }
  60% {
    -webkit-transform: scale(.98);
            transform: scale(.98);
  }
  70% {
    -webkit-transform: scale(.985);
            transform: scale(.985);
  }
  80% {
    -webkit-transform: scale(.99);
            transform: scale(.99);
  }
  90% {
    -webkit-transform: scale(.995);
            transform: scale(.995);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes zoominout {
  0% {
    -webkit-transform: scale(.95);
            transform: scale(.95);
  }
  10% {
    -webkit-transform: scale(.955);
            transform: scale(.955);
  }
  20% {
    -webkit-transform: scale(.96);
            transform: scale(.96);
  }
  30% {
    -webkit-transform: scale(.965);
            transform: scale(.965);
  }
  40% {
    -webkit-transform: scale(.97);
            transform: scale(.97);
  }
  50% {
    -webkit-transform: scale(.975);
            transform: scale(.975);
  }
  60% {
    -webkit-transform: scale(.98);
            transform: scale(.98);
  }
  70% {
    -webkit-transform: scale(.985);
            transform: scale(.985);
  }
  80% {
    -webkit-transform: scale(.99);
            transform: scale(.99);
  }
  90% {
    -webkit-transform: scale(.995);
            transform: scale(.995);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.transparent {
  opacity: 0;
}


