.footer {
	background: #000000;
	color: #ffffff;
}

.bottomText {
	text-align: center;
}

.links {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: start;
	    align-items: flex-start;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	margin-bottom: 4vw;
}

.theLinks {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: start;
	    align-items: flex-start;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.linksTitle {
	font-size: 2vw;
}

.linksTitle,
.initiative,
.bottomText {
	margin-top: 1vw;
	padding: 1vw 0;
}

.bottomText span {
	cursor: pointer;
	-webkit-transition: color .2s ease-in-out;
	-o-transition: color .2s ease-in-out;
	transition: color .2s ease-in-out;
}

.bottomText span:hover {
	color: #7fbb00;
}

.theLinks a {
	text-decoration: none;
	color: #ffffff;
	padding: .5vw;
	-webkit-transition: color .2s ease-in-out;
	-o-transition: color .2s ease-in-out;
	transition: color .2s ease-in-out;
}

.theLinks a:hover {
	text-decoration: underline;
	color: #7fbb00;
}

.ztmLogo img {
	max-width: 14vw;
	max-height: 14vw;
	width: 100%;
	height: 100%;
	opacity: .8;
	border-radius: 1vw;
	cursor: pointer;
}



